<template>
  <input type="text" :value="modelValue" @input="updateInput" class="input" />
</template>

<script>
export default {
  name: "my-input",
  props: {
    modelValue: [String, Number],
  },
  methods: {
    updateInput(event) {
      this.$emit('update:modelValue', event.target.value)
    },
  },
};
</script>

<style scoped>
.input {
  width: 100%;
  border: 2px solid #000;
  padding: 15px 15px;
  margin-top: 15px;
}
</style>